@import url(https://fonts.googleapis.com/css?family=Lato:400,700&subset=latin-ext);
.header {
    height: 10vh;
    padding-left: 10px;
}

.header__img {
    height: 100%;
}

@media only screen and (min-width:480px)and (orientation:landscape) {
    .header {
        width: 80%;
        margin: 0 auto;
    }
}

@media only screen and (min-width:480px)and (orientation:landscape) {
    .header {
        width: 70%;
    }
}

@media only screen and (min-width:1280px)and (orientation:landscape) {
    .header {
        width: 55%;
    }
}
.search-form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 100vw;
    -webkit-flex-basis: 50vw;
            flex-basis: 50vw;
    color: #fff;
    background-color: #222;
}

.search-form__form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    height: 40px;
    width: 60%;
    background-color: transparent;
}

.search-form__input,
.search-form__button {
    border: 1px solid transparent;
    height: 100%;
    font-size: 8px;
}


.search-form__header {
    padding-top: 10px;
    font-size: 14px;
}

.search-form__input {
    width: 70%;
    border-radius: 20px 0 0 20px;
    background-color: #fff;
    padding-left: 5%;
}

.search-form__button {
    width: 30%;
    color: #fff;
    background-color: orange;
    border-radius: 0 20px 20px 0;
}

.search-form__instruction {
    width: 70%;
    font-size: 8px;
    text-align: center;
    padding-bottom: 5px;
}

@media only screen and (min-width:411px)and (orientation:portrait) {

    .search-form__header {
        font-size: 18px;
    }

    .search-form__form {
        width: 60%;
        height: 40px;
    }

    .search-form__input {
        border-radius: 40px 0 0 40px;
    }

    .search-form__button {
        border-radius: 0 40px 40px 0;
    }

    .search-form__instruction {
        width: 70%;
        font-size: 12px;
        padding-bottom: 15px;
    }
}

@media only screen and (min-width:768px)and (orientation:portrait) {

    .search-form {
        padding: 5% 0;
    }

    .search-form__header {
        font-size: 24px;
    }

    .search-form__form {
        height: 60px;
    }

    .search-form__input,
    .search-form__button {
        font-size: 14px;
    }


}

@media only screen and (min-width:480px)and (orientation:landscape) {

    .search-form {
        padding: 0 10%;
        -webkit-flex-basis: 20vw;
                flex-basis: 20vw;
    }

    .search-form__header {
        padding-top: 5px;
        font-size: 14px;
    }

    .search-form__form {
        width: 30%;
        -webkit-flex-basis: 25%;
                flex-basis: 25%;

    }

    .search-form__instruction {
        width: 75%;
        font-size: 8px;

    }
}

@media only screen and (min-width:640px)and (orientation:landscape) {

    .search-form__header {
        font-size: 20px;
    }

    .search-form__form {
        width: 25%;
        height: 30px;
    }
}

@media only screen and (min-width:1024px)and (orientation:landscape) {
    .search-form {
        -webkit-justify-content: space-evenly;
                justify-content: space-evenly;
    }

    .search-form__header {
        font-size: 24px;
    }

    .search-form__form {
        width: 20%;
        height: 25px;
    }

    .search-form__instruction {
        width: 50%;
        font-size: 12px;
    }
}

@media only screen and (min-width:1280px)and (orientation:landscape) {
    .search-form {
        -webkit-flex-basis: 20vh;
                flex-basis: 20vh;
    }

    .search-form__header {
        font-size: 26px;
    }

    .search-form__form {
        width: 25%;
        height: 40px;
    }

    .search-form__input,
    .search-form__button {
        font-size: 12px;
    }

    .search-form__instruction {
        width: 55%;
    }
}

@media only screen and (min-width:1366px)and (orientation:landscape) {
    .search-form__form {
        width: 22%;
        height: 40px;
    }
}

@media only screen and (min-width:1920px)and (orientation:landscape) {

    .search-form__header {
        font-size: 30px;
    }

    .search-form__form {
        width: 20%;
        height: 50px;
    }

    .search-form__input {
        border-radius: 40px 0 0 40px;

    }

    .search-form__button {
        border-radius: 0 40px 40px 0;
    }

}
.results-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin-top: 30px;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}


.result {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-basis: 20%;
            flex-basis: 20%;
    width: 80vw;
    padding: 10px;
    margin-bottom: 10px;
    background-color: #eee;
    box-shadow: 0 0 4px #eee;
    border: 1px solid transparent;
}

.result:hover {
    cursor: pointer;
    border: 1px solid orange;
}

.result__a {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 100%;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    text-decoration: none;
    color: #000;
}

.result__description {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding-left: 5px;
}

.result__img {
    -webkit-flex-basis: 50%;
            flex-basis: 50%;
    height: 100%;
}

.result__title {
    font-size: 14px;
    text-align: center;
    padding-bottom: 5px;
    font-weight: 700;
    border-bottom: 1px solid #ccc;
}

.result__artist {
    font-size: 11px;
    text-align: center;
    font-weight: 300;
    padding-top: 5px;
}

@media only screen and (min-width:411px) and (orientation:portrait) {
    .result {
        width: 50vw;
    }
}


@media only screen and (min-width:768px) and (orientation:portrait) {
    .results-container {
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        width: 80%;
        margin: 40px auto;
    }

    .result {
        -webkit-flex-basis: 40%;
                flex-basis: 40%;
    }
}

@media only screen and (min-width:480px) and (orientation:landscape) {
    .results-container {
        margin: 40px auto 0 auto;
        width: 60vw;
    }

    .result {
        width: 80%;
    }

    .result__img {
        width: 100%;
    }
}

@media only screen and (min-width:768px) and (orientation:landscape) {
    .results-container {
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        width: 80vw;
    }

    .result {
        -webkit-flex-basis: 30%;
                flex-basis: 30%;
    }
}

@media only screen and (min-width:1280px) and (orientation:landscape) {
    .results-container {
        width: 60vw;
        height: 50vh;
        margin-top: 20px;
    }

    .result {
        -webkit-flex-basis: 26%;
                flex-basis: 26%;
    }

    .result__img {
        width: 15%;
    }

}
.footer {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100vw;
    -webkit-flex-basis: 10%;
            flex-basis: 10%;
    color: #fff;
    background-color: #222;
}

.footer__paragraph {
    text-align: center;
    font-size: 14px;
}

.footer__line {
    margin: 0 8px;
    width: 25px;
    height: 1px;
    border-top: 1px solid #fff;
}

@media only screen and (min-width:1280px) and (orientation:landscape) {
    .footer {
        -webkit-flex-basis: 7vh;
                flex-basis: 7vh;
    }
}
.results-value {
    padding-top: 20px;
    font-size: 16px;
    font-weight: 300;
    text-align: center;
}
.pagination {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    height: 30px;
}

.pagination__prev,
.pagination__next {
    height: 20px;
    width: 50px;
    background-color: orange;
    color: #fff;
    margin: 0 5px 15px 0;
    border-radius: 5px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Lato", sans-serif;
  min-height: 100vh;
}

.wrapper {
  display: -webkit-flex;
  display: flex;
  width: 100vw;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
}

@media only screen and (min-width:1280px) and (orientation:landscape) {
  .wrapper {
    height: 100vh;
  }

}
